.authContainer {
  background-image: url(../img/login_back.jpg);
  background-size: cover;
  background-position: center center;
}
.authContainer .alert {
  margin-bottom: 0px;
}
.authContainer .back-mountain {
  background-image: url(../img/back_mountain.jpg);
  background-size: cover;
  background-position: center center;
}
.authContainer .captcha-wrapper:hover .popup2 {
  display: inline;
}
.authContainer .popup2 {
  bottom: 5px;
  position: absolute;
  right: 120px;
  display: none;
}
.authContainer .back-mountain:after {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(1, 82, 118, 0.55);
  z-index: 0;
}
.authContainer #verifyCode::-webkit-input-placeholder {
  /* Most modern browsers support this now. */
  color: #9e9e9e!important;
}
.authContainer #verifyCode:-ms-input-placeholder {
  /* Most modern browsers support this now. */
  color: #9e9e9e!important;
}
.authContainer #verifyCode::-ms-input-placeholder {
  /* Most modern browsers support this now. */
  color: #9e9e9e!important;
}
.authContainer #verifyCode::placeholder {
  /* Most modern browsers support this now. */
  color: #9e9e9e!important;
}
.authContainer #verifyCode {
  padding-left: 4px;
  margin-bottom: 8px;
}
.authContainer #verifyCode:focus {
  outline: none !important;
  border: 2px solid #009688;
}



